:root {
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.link {
  color: #fff;
  width: 100px;
  text-align: center;
  background-color: #00f;
  border-radius: 5px;
  margin: 20px 0 0 20px;
  padding: 7px 12px;
  text-decoration: none;
  display: inline-block;
}

.form {
  width: 300px;
  flex-direction: column;
  margin-top: 50px;
  margin-left: 50px;
  display: flex;
}

.form-input {
  width: 300px;
  margin-bottom: 20px;
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.form-textarea {
  width: 300px;
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.form-text {
  margin-bottom: 5px;
